*{box-sizing:border-box}html{height:100%}body{font-size:112.5%;font-family:'Roboto', sans-serif;background:linear-gradient(18deg, #028a9c 4%, #014c73 33%, #028a9c 4%, #017a8b 57%, #018992 62%, #00ffcf 87%) fixed no-repeat;position:relative}main{margin:30px;height:100%}.darkmode{background:darkgrey}.p-0{padding:0}.mb-150{margin-bottom:150px !important}.pb-30{padding-bottom:30px}.hidden{display:none}.header{font-family:'Bungee Shade', cursive;position:sticky}.header__headline{text-align:center;font-size:250%;text-transform:uppercase}.footer{background-color:#8ce5c4;padding:15px;width:auto;margin:20px 0 0 0;position:fixed;bottom:0;left:0;right:0}.footer__nav{display:flex;text-align:center;justify-content:space-around;align-items:center;width:100%;max-width:100%}.footer__nav img{width:100%;height:40px}.svg-filled{fill:black;color:black;width:40px;height:40px}.card{background-color:white;border-radius:10px;padding:30px;border-style:solid;border-color:grey;margin:0 20px 60px 20px;position:relative;box-shadow:6px 4px 6px rgba(1,57,95,0.2)}.card__answer-container{text-align:center}.card__headline{font-weight:bolder}.card__answer-button{background-color:#8ff2ee;border-radius:4px;display:inline-block;padding:6px 15px;text-align:right;margin:20px;font-size:80%;cursor:pointer}.card__bookmark-button{position:absolute;top:0;right:1.5rem;height:70px;width:50px;-webkit-transform:rotate(0deg) skew(0deg);transform:rotate(0deg) skew(0deg);border-left:25px solid grey;border-right:25px solid grey;border-bottom:25px solid white}.card__bookmark-button--active{border-left:25px solid #0a5552;border-right:25px solid #0a5552}.tag-list{list-style-type:none;line-height:2em;position:relative;left:auto;right:auto}.tag-list li{display:inline-block;padding:5px 20px;border:1px solid #8ac4ff;border-radius:8px;background-color:#8de4ff;margin:10px;white-space:nowrap}.profile__intro{display:flex}.profile__intro--image{border-radius:50%;width:150px;height:150px;border:4px solid lightgrey;display:block}.profile__intro--name{margin-left:20px}.logout{text-align:center}.logout__button{background-color:#8ff2ee;border-radius:4px;display:inline-block;padding:6px 15px;font-size:80%;cursor:pointer}.create{text-align:center;margin-bottom:80px}.create__form{display:flex;flex-direction:column}.create__form--input{margin:10px 0 30px 0}.create__label{font-weight:bolder}.submit-button{background-color:#8ff2ee;border-radius:4px;display:inline-block;padding:6px 15px;margin:50px 20px 20px 20px;font-size:80%;cursor:pointer}#question,#answer,#tags{font-family:'Roboto', sans-serif}
